export const isCurrentMonth = (day, month) => day.month.month === month;
export const isWeekend = (day) => day.dayInWeek === 6 || day.dayInWeek === 0;
export const isSelected = (weekDay, { day, year, month }) => day === weekDay.dayInMonth &&
    month === weekDay.month.month &&
    year === weekDay.month.year;
export const monthName = (year, month) => new Date(year, month - 1).toLocaleString('en-US', { month: 'long' });
export const dayNames = (startOfTheWeek) => {
    const days = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];
    for (let i = 6; i > 6 - startOfTheWeek; i--) {
        const day = days.shift();
        if (day) {
            days.push(day);
        }
    }
    return days;
};
